
import { mapGetters, mapMutations } from "vuex";
import XProgress from 'vux/src/components/x-progress/index.vue'


export default {
data() {
return {
duration: 0
};
},
computed: {
...mapGetters(["currentMusic", "currentTime", "playerStatus"]),
percent() {
return Number(((this.currentTime / this.duration) * 100).toFixed(2));
}
},
methods: {
...mapMutations([
"setCurrentTime",
"changePlayerStatus",
"setMiniPlayer"
]),
timeupdate(e) {
this.duration = e.target.duration;
this.setCurrentTime(e.target.currentTime);
},
gotoPlayerPage() {
this.setMiniPlayer(false);
this.$router.push({
path: "/player"
});
}
},
watch: {
playerStatus(status) {
let audio = this.$refs.player;
if (status) {
audio.play();
} else {
audio.pause();
}
}
},
components: {
XProgress
}
};
